#data {
    display: none;
}

.timetable__calendar-wrap {
    height: 500px;
    .timetable__calendar, .pika-single {
        height: 100%;
    }
    .timetable__calendar {
        .pika-single {
            z-index: 1;
            border: none;
            border-bottom: 1px solid #ececec;
            .pika-lendar {
                .pika-title {
                    margin-bottom: 20px;
                    position: static;
                    .arrow-next.arrow--brown, .arrow-prev.arrow--brown {
                        background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxNSIgaGVpZ2h0PSIxMiIgdmlld0JveD0iMCAwIDE1IDEyIj48dGl0bGU+QXJ0Ym9hcmQgNTwvdGl0bGU+PHBhdGggZD0iTTcuNSAxMS43bC03LTguOGExLjUgMS41IDAgMSAxIDIuNC0xLjhsNC42IDUuOCA0LjYtNS44YTEuNSAxLjUgMCAxIDEgMi40IDEuOXoiICBmaWxsPSIjMmIyYjJiIi8+PC9zdmc+);
                        width: 20px;
                        height: 20px;
                        transition: .3s;
                    }
                    .arrow-next.arrow--brown:not(.is-disabled):hover,
                    .arrow-prev.arrow--brown:not(.is-disabled):hover {
                        opacity: 0.5;
                    }
                    .pika-next, .pika-prev {
                        transform: translate(0, 50%) rotate(-90deg);
                        font-size: 0;
                        opacity: 1;
                        position: absolute;
                        top: 215px;
                    }
                    .pika-next {
                        right: -90px;
                    }
                    .pika-prev {
                        transform: translate(0, 50%) rotate(90deg);
                        left: -90px;
                    }
                    .pika-prev.is-disabled, .pika-next.is-disabled {
                        cursor: default;
                        opacity: .2;
                    }
                    .pika-label {
                        color: map_get($gray, default);
                        font-family: $merri-font;
                        font-size: 24px;
                        font-weight: 700;
                        select {
                            top: 0px;
                        }
                    }
                }
                width: calc(50% - 200px);
                margin: 40px 100px;
                position: relative;
                &:first-child:after {
                    content: "";
                    display: block;
                    position: absolute;
                    right: -100px;
                    top: 0;
                    width: 1px;
                    height: 100%;
                    background-color: #ececec;
                }
                table {
                    thead {
                     tr th {
                        abbr {
                            border-bottom: none;
                            cursor: default;
                            text-decoration: none;
                            color: #777777;
                            font-size: 16px;
                            font-weight: 400;
                        }
                     }
                    }
                    tbody {
                        td {
                            position: relative;
                            width: 57px;
                            height: 57px;
                            &.is-disabled .pika-button {
                                border: none !important;
                            }
                            &:not(.is-disabled)
                             :not(.has-selected)
                             :not(.has-event) .pika-button {
                                border: 1px solid transparent;
                            }
                            &:not(.is-disabled):hover .pika-button {
                                background: rgba(0, 172, 193, 0.2);
                            } 
                            &.is-today:not(.is-disabled):hover .pika-button {
                                background-color: #f3f3f3;
                            }
                        }
                        
                        .pika-button {
                            padding: 11px;
                            color: #2b2b2b;
                            font-size: 16px;
                            line-height: 16px;
                            height: 57px;
                            text-align: center;
                            background: #fff;
                            font-weight: 500;
                            border: 1px solid transparent;
                            transition: border .3s, background .3s;
                        }
                        .is-selected .pika-button, .has-event .pika-button {
                            background: #fff;
                            box-shadow: none;
                            position: relative;
                            z-index: 10;
                            border-radius: 3px;
                            border: 1px solid map_get($blue, default);
                        }
                        .is-today.has-event.is-selected, .is-today.is-selected {
                            .pika-button {
                                background-color: #f3f3f3;
                                &:after {
                                    display: none;
                                }
    
                            }
                        }
                        .has-event .pika-button {
                            &:before, &:after {
                                display: block;
                                position: absolute;
                                text-align: center;
                                color: map_get($blue, default);
                                font-size: 10px;
                                font-weight: 500;
                                line-height: 12px;
                                width: 100%;
                                left: 0;
                            }
                            &:before {
                                content: "Мастер";
                                top: 3px;
                            }
                            &:after {
                                content: "класс";
                                bottom: 3px;
                            }
                        }
                        .is-disabled:hover {
                            .pika-button {
                                border: none;
                            }
                        }
                        .is-today {
                            .pika-button {
                                background-color: #f3f3f3;
                            }
                        }
                    }
                }
            }
        }
    }
}





