@import 'vars';
@import 'mixins';

.page-title {
    margin: 35px 0 35px 0;
}
.our-studio__information {
    padding: 30px 170px 65px;
    background-color: #fff;
    .page-title {
        padding-left: 0;
        font-size: 34px;
    }
    p {
        line-height: 20px;
        text-align: justify;
    }
    .information--adress {
        margin-top: 40px;
        .information__note {
            color: map_get($gray-light, active);
            font-size: 16px;
        }
    }
    .information--hotel {
        margin-top: 40px;
        width: 550px;
        .see-all-link {
            font-size: 16px;
        }
    }
}

.contact-section {
    margin-top: 35px;
    margin-bottom: 40px;
}

.subscribe {
    margin-top: 20px;
    padding: 50px 50px 60px 80px;
    background-color: #ffffff;
    text-align: center;
    .block-title {
        margin-bottom: 65px;
    }
}

@import 'parts/_calendar-booking';


.calendar {
    .timetable__calendar-wrap {
        .timetable__calendar .pika-single {
            border-bottom: none;
        }
    }
}


.studio-photos {
    // margin-top: 30px;
    .gallery-grid-wrap {
        margin-top: 25px;
        background-color: #fff;
        height: 390px;
        overflow: hidden;
        transition: all .4s;  
    }
    .gallery-grid {
        height: 100%; 
    }
    .btn-center-block {
        margin: 0;
        padding: 20px 0;
        background-color: #fff;
    }
}

